
<template>
  <v-container
  id='contain'
  fluid>
    <Timeline v-if="trackingData"
    id="timeline"
    ></Timeline>
    <div id="plotter"></div>
  </v-container>
</template>
<script>

/* eslint-disable */
 import {mapGetters} from 'vuex'
import Plotly from 'plotly.js'
import Timeline from '@/components/Timeline.vue'

export default { 
  components: {
    'Timeline': Timeline
  },
  computed: mapGetters({
    sliderTime: 'sliderTime',
    btnState: 'btnState',
    trackingData: 'trackingData',
    maxTime: 'maxTime',
    displayOptions: 'displayOptions',
    routeData: 'routeData'
  }),
  watch: {
    sliderTime:{
      handler (val) {
        this.sliderState = this.sliderTime
        if(val === this.maxTime){
          clearInterval(this.playLooper)
          this.$store.commit('resetTime')
        } else {
          this.sliderAnimator(val)
        } 
      },
      deep: true
    },
    btnState:{
      handler (val) {
        // check if button is resting(play state is showing)
        if (val === true) {
        //if slider is at end, reset slider when play is pressed
        if(this.sliderState === this.maxTime){
          this.sliderState = 0
        }
        this.playFunc()
        } 
        else {
        clearInterval(this.playLooper)
        // Plotly.animate('plotter', [], {mode: 'immediate'})
        }
      },
      deep: true
    },
    displayOptions: {
      handler(val){
        console.log(val, "from watcher")
        this.displayHandler(val)
      },
      deep: true
    }
    },
  data () {
    return {
      masterShapeHolder: [],
      masterNumHolder: [],
      hashWidth: .667,
      hashDistance: 3.08,
      hashHeight: .111,
      traces: [],
      aframes: [1],
      gd: {},
      layout: {},
      playLooper: '',
      sliderState: 0,
      offensePlayersx: [],
      offensePlayersy: [],
      defensePlayersx: [],
      defensePlayersy: [],
      offJersey: [],
      defJersey: []
    }
  },
  methods: {
    playFunc () {
      let self = this
      this.playLooper = window.setInterval(()=>this.sliderState++, 15)
      },
    sliderAnimator(sliderVal){
      Plotly.animate('plotter', [this.aframes[sliderVal].name],{
        frame: 
            {
              duration: 1,
              redraw: false
              }
        ,
        transition: 
          {
            duration: 1,
          }
        ,
        mode: 'immediate'
      })
      },
    populatePlayers (i, offensePlayersx, offensePlayersy, defensePlayersx, defensePlayersy, offPos, defPos){
      //push vals to aframes
      this.aframes.push({
      data: [
        {mode: 'markers+text',
        name: 'QB',
        x: [this.trackingData['qbtrackingdata'][i]['x']],
        y: [this.trackingData['qbtrackingdata'][i]['y']],
        text: 'QB',
        marker: {
          color: 'cyan',
          label: 'QB',
          size: 24
        }
      },
      {
        mode: 'markers+text',
          name: 'Offense',
          x: offensePlayersx,
          y: offensePlayersy,
          text: offPos,
          textfont: {
          color: '#ffffff'
        },
          visible: this.offenseVisible,
          marker: {
            color: 'red',
            size: 24
          }
      },{
        mode: 'markers+text',
        name: 'Defense',
        x: defensePlayersx,
        y: defensePlayersy,
        text: defPos,
        textfont: {
          color: '#ffffff'
        },
        visible: this.defenseVisible,
        marker: {
          color: 'blue',
          size: 24,
      }
      },
      {
        mode: 'markers+text',
          name: 'Ball',
          x: [this.trackingData['balltrackingdata'][i]['simulated_ball'].x],
          y: [this.trackingData['balltrackingdata'][i]['simulated_ball'].y],
          marker: {
            color: 'yellow',
            size: 14
          }
      }
      ],
      name: i.toString()
      })
      },
    YardMachine(){
        let yardline = this.trackingData.balltrackingdata["0"].simulated_ball.y
        let firstDown = this.trackingData.balltrackingdata["0"].simulated_ball.y + this.trackingData.playsituation.distance
        this.masterShapeHolder.push({
        // sides
              type: 'rect',
              layer: 'below',
              xref: 'x',
              x0: -26.6,
              x1: -27.6,
              yref: 'y',
              y0: -60,
              y1: 60,
              fillcolor: '#ffffff',
              line: {
                color: "#ffffff"
              }
            },
            {
              type: 'rect',
              layer: 'below',
              xref: 'x',
              x0: 26.6,
              x1: 27.6,
              yref: 'y',
              y0: -60,
              y1: 60,
              fillcolor: '#ffffff',
              line: {
                color: "#ffffff"
              }
            },
            //top&bottom
            {
              type: 'rect',
              layer: 'below',
              xref: 'x',
              x0: -27.6,
              x1: 27.6,
              yref: 'y',
              y0: -60,
              y1: -61,
              fillcolor: '#ffffff',
              line: {
                color: "#ffffff"
              }
            },
            {
              type: 'rect',
              layer: 'below',
              xref: 'x',
              x0: -27.6,
              x1: 27.6,
              yref: 'y',
              y0: 60,
              y1: 61,
              fillcolor: '#ffffff',
              line: {
                color: "#ffffff"
              }
            },
            // first down markers
            {
              type: 'rect',
              layer: 'below',
              xref: 'x',
              x0: -26.6,
              x1: 26.6,
              yref: 'y',
              y0: yardline,
              y1: yardline + this.hashHeight,
              fillcolor: 'rgba(0,40,133,.5)',
              line: {
                color: "rgba(0,40,133,.7)"
              }
            },
            {
              type: 'rect',
              layer: 'below',
              xref: 'x',
              x0: -26.6,
              x1: 26.6,
              yref: 'y',
              y0: firstDown,
              y1: firstDown + this.hashHeight,
              fillcolor: 'rgba(245,236,0,.5)',
              line: {
                color: 'rgba(245,236,0,.5)'
              }
            }
            )
      let cnt = 0
      for(let i = 0; i <= 20; i++){
      console.log("does this even run?")
      this.masterShapeHolder.push({
        type: 'rect',
        layer: 'below',
        xref: 'x',
        x0: -26.6,
        x1: 26.6,
        yref: 'y',
        y0: -50 + cnt,
        y1: -50 - this.hashHeight + cnt,
        fillcolor: '#ffffff',
        line: {
          color: "#ffffff"
        }
      })
      if(i < 18){
        //inner hashes
        this.masterShapeHolder.push({
        type: 'rect',
        layer: 'below',
        xref: 'x',
        x0: this.hashDistance,
        x1: this.hashDistance + .05,
        yref: 'y',
        y0: -44.7 + cnt,
        y1: -45.3 + cnt,
        fillcolor: '#ffffff',
        line: {
          color: "#ffffff"
        }
        },
        {
        type: 'rect',
        layer: 'below',
        xref: 'x',
        x0: -this.hashDistance,
        x1: -this.hashDistance - .05,
        yref: 'y',
        y0: -44.7 + cnt,
        y1: -45.3 + cnt,
        fillcolor: '#ffffff',
        line: {
          color: "#ffffff"
        }}
        )
      }
      cnt += 5
      }
      },
    HashMachine(){
      let cnt = 0
      let self = this
      for(let i =0; i < 101; i++){
          //side hashes
          //left
          this.masterShapeHolder.push({
            type: 'rect',
            layer: 'below',
            xref: 'x',
            x0: -26 - self.hashWidth,
            x1: -26,
            yref: 'y',
            y0: -50 + cnt,
            y1: -50 - self.hashHeight + cnt,
            fillcolor: '#ffffff',
            line: {
              color: "#ffffff"
            }
          },
            //right
          {
            type: 'rect',
            xref: 'x',
            layer: 'below',
            x0: 26 + self.hashWidth,
            x1: 26,
            yref: 'y',
            y0: -50 + cnt,
            y1: -50 - self.hashHeight + cnt,
            fillcolor: '#ffffff',
            line: {
              color: "#ffffff"
            }
          },
          //middle hashes
          {
            type: 'rect',
            xref: 'x',
            layer: 'below',
            x0: self.hashDistance,
            x1: self.hashDistance + self.hashWidth,
            yref: 'y',
            y0: -50 + cnt,
            y1: -50.05 + cnt,
            fillcolor: '#ffffff',
            line: {
              color: "#ffffff"
            }
          },
          {
            type: 'rect',
            xref: 'x',
            x0:  -self.hashDistance,
            x1: -self.hashDistance - self.hashWidth,
            layer: 'below',
            yref: 'y',
            y0: -50 + cnt,
            y1: -50.05 + cnt,
            fillcolor: '#ffffff',
            line: {
              color: "#ffffff"
            }
          }
          )
            cnt += 1
          }
        // bottom and top mid hash
      this.masterShapeHolder.push({
        type: 'rect',
        xref: 'x',
        x0: 0,
        x1: 0.2,
        yref: 'y',
        layer: 'below',
        y0: 48,
        y1: 47.95,
        fillcolor: '#ffffff',
        line: {
          color: "#ffffff"
        }},{
        type: 'rect',
        xref: 'x',
        layer: 'below',
        x0: 0.2,
        x1: 0,
        yref: 'y',
        y0: -48,
        y1: -47.95,
        fillcolor: '#ffffff',
        line: {
          color: "#ffffff"
            }})
      },
    NumMachine(){
      let cnt = 0
      let cnt1 = 0
      const numberSize = 1.5
      const zero = "data:image/png;base64,"
      const left1 = 'data:image/png;base64,'
      const left2 = 'data:image/png;base64,'
      const left3 = 'data:image/png;base64,'
      const left4 = 'data:image/png;base64,'
      const left5 = 'data:image/png;base64,'
      const right1 = 'data:image/png;base64,'
      const right2 = 'data:image/png;base64,'
      const right3 = 'data:image/png;base64,'
      const right4 = 'data:image/png;base64,'
      const right5 = 'data:image/png;base64,'
      const fieldNumsL = [left1, left2, left3, left4, left5]
      const fieldNumsR = [right1, right2, right3, right4, right5]
      for(let i = 0; i < 9; i++){ 
        this.masterNumHolder.push({
        //zeros
          //right
            layer: 'below',
            x: 12,
            y: -38.2 + cnt,
            sizex: numberSize,
            sizey: numberSize,
            source: zero,
            sizing: 'stretch',
            xref: 'x',
            yref: 'y'
          },
          //left
          {
            layer: 'below',
            x: -14,
            y: -40.4 + cnt,
            sizex: numberSize,
            sizey: numberSize,
            source : zero,
            sizing: 'stretch',
            xref: 'x',
            yref: 'y'
          },{
        // other numbers
          //right
            layer: 'below',
            x: 12,
            y: -40.4 + cnt,
            sizex: numberSize,
            sizey: numberSize,
            source : fieldNumsR[cnt1],
            sizing: 'stretch',
            xref: 'x',
            yref: 'y'
          },
          //left
          {
            layer: 'below',
            x: -14,
            y: -38.2 + cnt,
            sizex: numberSize,
            sizey: numberSize,
            source : fieldNumsL[cnt1],
            sizing: 'stretch',
            xref: 'x',
            yref: 'y'
          })
        cnt += 10
        cnt1++
        if(cnt1 > 4){
          fieldNumsL.reverse()
          fieldNumsR.reverse()
          cnt1 = 1
        }
      }
      },
    triangleMachine(){
      const triangleDown = 'data:image/png;base64,'
      const triangleUp = 'data:image/png;base64,'
      let cnt = 0
      for(let i = 1; i < 5; i++){
        //push triangles
        this.masterNumHolder.push({
            layer: 'below',
            x: -13.7,
            y: 13.4 + cnt,
            sizex: .8,
            sizey: 1,
            source : triangleUp,
            sizing: 'stretch',
            xref: 'x',
            yref: 'y'
      },
      {
            layer: 'below',
            x: 12.4,
            y: 13.4 + cnt,
            sizex: .8,
            sizey: 1,
            source : triangleUp,
            sizing: 'stretch',
            xref: 'x',
            yref: 'y'
      },
      {
            layer: 'below',
            x: -13.7,
            y: -42.4 + cnt,
            sizex: .8,
            sizey: 1,
            source : triangleDown,
            sizing: 'stretch',
            xref: 'x',
            yref: 'y'
      },
      {
        
            layer: 'below',
            x: 12.4,
            y: -42.4 + cnt,
            sizex: .8,
            sizey: 1,
            source : triangleDown,
            sizing: 'stretch',
            xref: 'x',
            yref: 'y'
      }) 
      cnt += 10
      }
        },
    setPlayers (){
      this.aframes = []
      this.offJersey = []
      this.defJersey = []
      for(let i = 0; i < this.trackingData.playerroles.offense.length; i++){
        this.offJersey.push(this.trackingData.teamroster.offense[i].jersey)
      }
      for(let i = 0; i < this.trackingData.playerroles.defense.length; i++){
        this.defJersey.push(this.trackingData.teamroster.defense[i].jersey)
      }
      for(var i = 0; i < this.trackingData['balltrackingdata'].length; i++){
        //init arrs
        this.offensePlayersx = []
        this.offensePlayersy = []
        this.defensePlayersx = []
        this.defensePlayersy = []
        //populate off
        for(let j = 0; j < this.trackingData.playerroles.offense.length; j++){
          this.offensePlayersx.push(this.trackingData['playertrackingdata'][j]['playertracking'][i]['x'])
          this.offensePlayersy.push(this.trackingData['playertrackingdata'][j]['playertracking'][i]['y'])
        }
        //populate def
        for(let j = this.trackingData.playerroles.offense.length; j <this.trackingData.playerroles.offense.length + this.trackingData.playerroles.defense.length; j++){
          this.defensePlayersx.push(this.trackingData['playertrackingdata'][j]['playertracking'][i]['x'])
          this.defensePlayersy.push(this.trackingData['playertrackingdata'][j]['playertracking'][i]['y'])
        }
      this.populatePlayers(i, this.offensePlayersx, this.offensePlayersy, this.defensePlayersx, this.defensePlayersy, this.offJersey, this.defJersey)
      }
      },
    displayHandler(val){
      const hide = {
        opacity: '0'
      }
      const show = {
        opacity: '1'
      }
      if(val.player.off === false){
        Plotly.restyle('plotter', hide, 1)
      } else {
        Plotly.restyle('plotter', show, 1)
      }
      if(val.player.def === false) {
        Plotly.restyle('plotter', hide, 2)
      } else {
        Plotly.restyle('plotter', show, 2)
      }
      //if offense turned off
      }
      

  },
////////////////////////////////////////////////////



















  ////////////////////////////MOUNTED
///////////////////////////////////////////////////
  mounted () {
    //set values in store
    this.$store.commit('fieldStart')
    this.$store.commit('setMaxTime', this.trackingData.balltrackingdata.length - 1)
    //check if frames have already been populated
    if(this.aframes.length < this.trackingData['balltrackingdata'].length){
    this.setPlayers()
  }
  console.log(this.fieldDisplayOptions, "field display option")
    // ===================================
    //            Field Set Up
    // ===================================
  // Creating Hash markers
  this.HashMachine()
   // populate numbers
  this.NumMachine()
  //yardLines
  this.YardMachine()
  // populate triangles(?)
  this.triangleMachine()

    // Layout
    this.layout = {
    paper_bgcolor: '#1b7c00',
    plot_bgcolor: 'rgba(0,0,0,0)',
    grid: {
      domain: {
        x:[-26.6, 26.6],
        y: [-60,60]
      }
    },
      xaxis: {
        range: [-26.6, 26.6],
        showgrid: false,
        showlegend: false,
        zeroline: false,
        ticks: '',
        fixedrange: true,
        showline: true,
        autotick: true,
        hoverlabel: true,
        showticklabels: false
      },
      // for deployment: this.routeData.MinY, this.routeData.MaxY
      // for testing: this.trackingData['qbtrackingdata'][this.sliderTime]['y'] -7.5,this.trackingData['qbtrackingdata'][this.sliderTime]['y'] + 20
      yaxis: {
        range: [this.trackingData['qbtrackingdata'][this.sliderTime]['y'] -7.5,this.trackingData['qbtrackingdata'][this.sliderTime]['y'] + 20],
        showgrid: false,
        showlegend: false,
        fixedrange: true,
        hoverlabel: true,
        zeroline: false,
        ticks: '',
        showline: true,
        autotick: true,
        showticklabels: false
      },
      margin: {
          t: 0, //top margin
          l: 0, //left margin
          r: 0, //right margin
          b: 0 //bottom margin
      },
      showlegend: false,
      images: this.masterNumHolder,
      shapes: this.masterShapeHolder
    }

    this.aframes[1] //sample data 1
    this.aframes[2] //sample data 2


    let self = this
    Plotly.plot('plotter', {
      data: this.aframes[0].data,
      layout: this.layout
    }).then(function(){
      Plotly.addFrames('plotter',self.aframes)
    })
    //make animation responsive
    window.onresize = function () {
      Plotly.Plots.resize('plotter')
    }
  }    
}
</script>

<style scoped>
#timeline {
  position: absolute;
  z-index: 1000;
  height: 0vh;
  width: 100%;
}
#timeline[data-v-8eeda45e]{
  width: 100%;
}
#contain {
  padding: 0 0;
  width: 100%;
  height: 85vh;
}
#plotter {
  margin-bottom: 0px;
  padding: 0px;
  width: 100%;
  height: 100vh;
}
</style>



